* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: Arial, Helvetica, sans-serif;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.app-container {
  min-height: 100vh; /* Minimum height of 100vh */
  height: auto;      /* Allow height to be determined by content */
  width: 100vw;
  overflow: auto;    /* Enable scrolling */
  scroll-behavior: smooth;
  overflow:hidden;
  display:flex;
  flex-direction:column;
  align-items:center;
  justify-content:center;
}

.shapesIcon {
  position: fixed;
  top: -52px;
  left: -54px;
  width: 235px;
  height: 173px;
}

.shapesIcon1 {
  position: fixed;
  height: 20.5%;
  width: 60.26%;
  right: -18.97%;
  bottom: -4.74%;
}

.page-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content:center;
  position: relative;
  padding: 0;
  height: auto;
}

:root {
  --pr-color: #32cd32;
  --sc-color: rgb(80, 128, 96);
}

@media (min-width: 1024px) {
  .app-container {
      width: 569px !important;
      margin: 0;
      background-color: lightblue;
      padding: 0;
      overflow: hidden;
      border: 1px solid black;
  }
}
